import * as React from "react";
import VideoPlayer from "../../components/common/VideoPlayer";
import TeamMemberCard from "../../components/common/TeamMemberCard";
import DynamicHorizontalGallery from "../../components/DynamicHorizontalGallery";
import ZoomableImage from "../../components/ZoomableImage";

// import Timeline from "../../components/common/Timeline";
import Navbar from "../../components/Header/Navbar";
import {
  Box,
  // Paper,
  Container,
  // alpha,
  useTheme,
  useMediaQuery,
  Typography
} from "@mui/material";
import { Client } from "../../globals";

export default function Team() {
  // const timelineEvents = Client.milestones;
  const teamMembers = Client.team;

  const galleryImages = [
    {
      src: '/images/tour/sala-attesa.jpg',
      alt: 'Sala d\'attesa',
      ImageListItemBarProps: {
        title: "Sala d'attesa"
      },
    },
    {
      src: '/images/tour/sala-operativa-1.jpg',
      alt: 'Sala Operativa 1',
      ImageListItemBarProps: {
        title: "Sala Operativa 1",
      },
    },
    {
      src: '/images/tour/sala-operativa-2.jpg',
      alt: 'Sala Operativa 2',
      ImageListItemBarProps: {
        title: "Sala Operativa 2",
      },
    },
    {
      src: '/images/tour/sala-sterilizzazione.jpg',
      alt: 'Sala di Sterilizzazione',
      ImageListItemBarProps: {
        title: "Sala di Sterilizzazione",
      },
    },
  ];

  const [galleryCols, setGalleryCols] = React.useState(4);
  const [galleryHeight, setGalleryHeight] = React.useState(400);

  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const isMediumScreen = useMediaQuery(theme.breakpoints.between('sm', 'md'));
  const isLargeScreen = useMediaQuery(theme.breakpoints.up('md'));

  React.useEffect(() => {
      if (isSmallScreen) {
        setGalleryCols(1);
        setGalleryHeight(300);
      } else if (isMediumScreen) {
        setGalleryCols(2);
        setGalleryHeight(300);
      } else if (isLargeScreen) {
        setGalleryCols(3);
        setGalleryHeight(400);
      }
    }, [isSmallScreen, isMediumScreen, isLargeScreen]);

  return (
    <>
      <Navbar />
      <Container>
        <Box>
          <VideoPlayer
            src="/videos/virtual-tour.mp4"
            controls
            videoProps={{
              style: { borderRadius: "1rem" },
              muted: true,
              autoPlay: true,
            }}
          />
        </Box>

        <Box sx={{height: galleryHeight}}
        >
          <DynamicHorizontalGallery
            cols={galleryCols}
            infinite
            Image={ZoomableImage}
            ImageListItemBarProps={{
              sx: { pointerEvents: "none" }, // let the events act to the underlying img
            }}
            BoxProps={{height: galleryHeight}}
            images={galleryImages}
          />
        </Box>
        {/* <Paper
          sx={{
            mt: 4,
            bgcolor: alpha(theme.palette.primary.main, 0.1),
            maxWidth: "800px",
            mx: "auto"
          }}
        >
          <Timeline events={timelineEvents} />
        </Paper> */}

        <Box sx={{ mt: 4 }}>
          <Typography variant="h4" sx={{ fontWeight: "bold" }}>
            Il nostro team
          </Typography>
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",
              gap: "1rem",
              alignItems: "stretch ",
              justifyContent: "center",
              padding: "2rem",
              px: {
                xs: 0,
                sm: 1,
                md: 3,
              }
              // [theme.breakpoints.up("md")]: {
              //   flexDirection: "row"
              // }
            }}
          >
            {teamMembers.map((person, index) => (
              <TeamMemberCard key={index} {...person} />
            ))}
          </Box>
        </Box>
      </Container>
    </>
  );
}
