import React, { useState, useEffect } from "react";
import {
  Box,
  ImageList,
  ImageListItem,
  IconButton,
  ImageListItemBar,
} from "@mui/material";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";

/**
 * Extracts a specific portion (slice) of an array based on the given starting index and desired slice length.
 *
 * @param {Array} array - The array from which to extract the slice.
 * @param {number} index - The starting index in the array from where the slice should begin.
 * @param {number} sliceLength - The number of elements to include in the slice.
 * @param {boolean} infiniteBehaviour - When overflowing the array, restart from the first element
 * @returns {Object} An object containing the extracted slice, the first index, and the last index.
 * @returns {Array} return.slice - The extracted portion of the array.
 * @returns {number} return.firstIndex - The starting index of the slice in the original array.
 * @returns {number} return.lastIndex - The ending index (inclusive) of the slice in the original array.
 */
const getArraySlice = (
  array,
  index,
  sliceLength,
  infiniteBehaviour = false
) => {
  let firstIndex = index;
  let lastIndex = index + sliceLength - 1;

  // Handle case where the slice extends beyond the end of the array
  if (!infiniteBehaviour && lastIndex + 1 > array.length) {
    firstIndex = array.length - sliceLength;
    lastIndex = array.length - 1;
  }

  // Ensure firstIndex does not become negative
  if (firstIndex < 0) {
    firstIndex = 0;
  }

  // Extract the slice from the array
  const slice = array.slice(firstIndex, lastIndex + 1);

  // If it has to behave as infinite: complete the slice
  if (infiniteBehaviour && slice.length < sliceLength) {
    const nItems = sliceLength - slice.length;
    for (let i = 0; i < nItems; i++) {
      slice.push(array[(lastIndex = i % array.length)]);
    }
  }

  return { slice, firstIndex, lastIndex };
};

/**
 * DefaultPrevButton component renders the default "Previous" button.
 *
 * @param {function} handleGoPrev - Function to handle the "Previous" button click.
 * @param {React.ElementType} PrevIcon - Icon component to render inside the button.
 */
const DefaultPrevButton = ({ handleGoPrev, PrevIcon }) => (
  <IconButton
    onClick={handleGoPrev}
    sx={{
      position: "absolute",
      left: 0,
      top: "50%",
      transform: "translateY(-50%)",
      zIndex: 2,
    }}
  >
    <PrevIcon />
  </IconButton>
);

/**
 * DefaultNextButton component renders the default "Next" button.
 *
 * @param {function} handleGoNext - Function to handle the "Next" button click.
 * @param {React.ElementType} NextIcon - Icon component to render inside the button.
 */
const DefaultNextButton = ({ handleGoNext, NextIcon }) => (
  <IconButton
    onClick={handleGoNext}
    sx={{
      position: "absolute",
      right: 0,
      top: "50%",
      transform: "translateY(-50%)",
      zIndex: 2,
    }}
  >
    <NextIcon />
  </IconButton>
);

/**
 * DynamicHorizontalGallery component renders a horizontal gallery from an array of images,
 * with navigation buttons to scroll through the images.
 *
 * @param {Object[]} images - Array of image objects to be rendered. Each object can contain:
 *  @param {string} [images[].alt] - Alternative text for the image.
 *  @param {string} [images[].src] - The source URL of the image.
 *  @param {string} [images[].srcSet] - The source set for the image, useful for responsive images.
 *  @param {string|number} [images[].key] - Unique key for the image list item.
 *  @param {Object} [images[].ImageListItemBarProps] - Props for the ImageListItemBar component.
 * @param {number} [cols] - Number of columns for the ImageList component. Defaults to the number of images.
 * @param {bool} [infinite] - Can move before the first and after the last.
 * @param {number} [imageIndex=0] - Index of the first image to be displayed from the array.
 * @param {Object} [ImageListProps] - Additional props to pass to the ImageList component.
 * @param {Object} [ImageListItemProps] - Additional props to pass to each ImageListItem component.
 * @param {Object} [ImageListItemBarProps] - Shared props for the ImageListItemBar component applied to all items.
 * @param {Object} [BoxProps] - Additional props to pass to the Box component.
 * @param {React.ElementType} [Image] - Custom component to render each image. Defaults to a simple img element.
 * @param {Object} [ImageProps] - Additional props to pass to the Image component.
 * @param {React.ElementType} [PrevButton] - Custom component to render the "Previous" button. Defaults to DefaultPrevButton.
 * @param {React.ElementType} [PrevIcon] - Custom icon component for the "Previous" button. Defaults to ArrowBackIosIcon.
 * @param {React.ElementType} [NextButton] - Custom component to render the "Next" button. Defaults to DefaultNextButton.
 * @param {React.ElementType} [NextIcon] - Custom icon component for the "Next" button. Defaults to ArrowForwardIosIcon.
 */
const DynamicHorizontalGallery = ({
  images, // Array of image objects
  cols, // Number of columns for the ImageList component
  infinite, // Switch to enable an infinite list
  imageIndex, // Index of the first image to be displayed
  ImageListProps, // Additional props for the ImageList component
  ImageListItemProps, // Additional props for each ImageListItem component
  ImageListItemBarProps, // Shared props for the ImageListItemBar component
  BoxProps, // Additional props for the Box component
  Image, // Custom component to render each image
  ImageProps, // Additional props for the Image component
  PrevButton, // Custom component to render the "Previous" button
  PrevIcon, // Custom icon component for the "Previous" button
  NextButton, // Custom component to render the "Next" button
  NextIcon, // Custom icon component for the "Next" button
}) => {
  // Set the number of columns to the length of images if cols is not provided
  cols ??= images.length;

  // Default to an <img> element if no custom Image component is provided
  Image ??= (props) => (
    <img loading="lazy" style={{ width: "100%", height: "100%" }} {...props} />
  );

  // Set default values for optional props
  infinite ??= false;
  imageIndex ??= 0;
  PrevButton ??= DefaultPrevButton;
  PrevIcon ??= () => <ArrowBackIosIcon color="white" />;
  NextButton ??= DefaultNextButton;
  NextIcon ??= () => <ArrowForwardIosIcon color="white" />;

  // State management
  const [currentIndex, setCurrentIndex] = useState(imageIndex);
  const [canGoPrev, setCanGoPrev] = useState(false);
  const [canGoNext, setCanGoNext] = useState(false);
  const [imagesSlice, setImagesSlice] = useState(null);

  // Function to update state based on the new index
  const updateState = (newIndex) => {
    newIndex = newIndex === -1 ? images.length - 1 : newIndex % images.length;

    console.log(newIndex);
    const { slice, firstIndex, lastIndex } = getArraySlice(
      images,
      newIndex,
      cols,
      infinite
    );
    setImagesSlice(slice);
    setCurrentIndex(firstIndex);
    setCanGoPrev(images.length && (infinite || firstIndex > 0));
    setCanGoNext(images.length && (infinite || lastIndex + 1 < images.length));
  };

  // Initial effect to set up the component's state
  useEffect(() => {
    updateState(imageIndex);
  }, [imageIndex]);

  // Handlers for navigating the gallery
  const handleGoPrev = () => updateState(currentIndex - 1);
  const handleGoNext = () => updateState(currentIndex + 1);

  return (
    <Box
      sx={{ overflowX: "auto", width: "100%", height:"100%", position: "relative" }}
      {...BoxProps}
    >
      {canGoPrev && (
        <PrevButton handleGoPrev={handleGoPrev} PrevIcon={PrevIcon} />
      )}
      <ImageList
        sx={{
          gridAutoFlow: "column",
          transform: "translateZ(0)",
          height: "100%",
          my: 0,
          overflowX: "hidden",
        }}
        cols={cols}
        {...ImageListProps}
      >
        {imagesSlice?.map((image, index) => (
          <ImageListItem
            sx={{height: "100%", contain: "size"}}
            {...ImageListItemProps}
            key={image.key || "img-" + index}
          >
            <Image {...ImageProps} {...image} />
            {image.ImageListItemBarProps && (
              <ImageListItemBar
                {...ImageListItemBarProps}
                {...image.ImageListItemBarProps}
              />
            )}
          </ImageListItem>
        ))}
      </ImageList>
      {canGoNext && (
        <NextButton handleGoNext={handleGoNext} NextIcon={NextIcon} />
      )}
    </Box>
  );
};

export default DynamicHorizontalGallery;
