import React, { useState } from "react";
import { Dialog as MUIDialog, Box, IconButton } from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";

/**
 * DefaultDialog component renders a fullscreen dialog with an image and a close button.
 *
 * @param {string} src - The source URL of the image to display.
 * @param {string} alt - Alternative text for the image.
 * @param {boolean} open - Whether the dialog is open or not.
 * @param {function} handleClose - Function to close the dialog.
 * @param {React.ElementType} Image - Component to render the image inside the dialog.
 * @param {Object} ImageProps - Additional props to pass to the Image component.
 * @param {Object} DialogProps - Additional props to pass to the MUI Dialog component.
 */
const DefaultDialog = ({
  src, // Source URL for the image
  alt, // Alt text for the image
  open, // Dialog open state
  handleClose, // Function to close the dialog
  Image, // Component to render the image
  ImageProps, // Additional props for the Image component
  ...DialogProps // Additional props for the Dialog component
}) => (
  <MUIDialog
    open={open}
    onClose={handleClose}
    aria-labelledby="zoomable-image-dialog"
    fullScreen
    sx={{ backgroundColor: "rgba(0, 0, 0, 0.75)" }}
    PaperProps={{
      style: {
        backgroundColor: "transparent",
        boxShadow: "none",
      },
    }}
    {...DialogProps}
  >
    <Box
      position="relative"
      display="flex"
      justifyContent="center"
      alignItems="center"
      height="100vh"
      width="100vw"
    >
      <IconButton
        aria-label="close"
        onClick={handleClose}
        sx={{
          position: "absolute",
          right: 8,
          top: 8,
          color: (theme) => theme.palette.grey[200],
        }}
      >
        <CloseIcon />
      </IconButton>
      <Image src={src} alt={alt} {...ImageProps} />
    </Box>
  </MUIDialog>
);

/**
 * ZoomableImage component provides functionality to display an image that can be zoomed into a fullscreen dialog on click.
 *
 * @param {string} src - The source URL of the image.
 * @param {string} alt - Alternative text for the image.
 * @param {React.ElementType} [UnzoommedImage] - Component to render the image before zooming. Defaults to a simple img element.
 * @param {React.ElementType} [ZoommedImage] - Component to render the image inside the dialog. Defaults to a simple img element.
 * @param {React.ElementType} [Dialog] - Component to render the dialog. Defaults to the provided DefaultDialog.
 * @param {Object} DialogProps - Additional props to pass to the Dialog component.
 * @param {Object} ImageProps - Additional props to pass to the image components (both unzoomed and zoomed).
 */
const ZoomableImage = ({
  src, // Source URL for the image
  alt, // Alt text for the image
  UnzoommedImage, // Component to render the unzoomed image
  ZoommedImage, // Component to render the zoomed image in the dialog
  Dialog, // Component to render the dialog
  DialogProps, // Additional props for the Dialog component
  ...ImageProps // Additional props for the image components
}) => {
  // Set default components if none are provided
  UnzoommedImage ??= (props) => (
    <img
      style={{ cursor: "zoom-in", width: "100%", height: "100%", objectFit: "cover", objectPosition: "center" }}
      {...props}
    />
  );
  ZoommedImage ??= (props) => (
    <img style={{ maxWidth: "100vw", maxHeight: "100vh" }} {...props} />
  );
  Dialog ??= DefaultDialog;

  // State to manage dialog open/close status
  const [open, setOpen] = useState(false);

  // Function to open the dialog
  const handleClickOpen = () => {
    setOpen(true);
  };

  // Function to close the dialog
  const handleClose = () => {
    setOpen(false);
  };

  return (
    <>
      <UnzoommedImage
        src={src}
        alt={alt}
        {...ImageProps}
        onClick={handleClickOpen}
      />
      <Dialog
        open={open}
        handleClose={handleClose}
        src={src}
        alt={alt}
        Image={ZoommedImage}
        ImageProps={ImageProps}
        {...DialogProps}
      />
    </>
  );
};

export default ZoomableImage;
